.grid {
  margin: 0;
  min-height: 100vh;
  padding:0 10% 0 10%;
  display: grid;

  grid-template-areas: 
    "header header"
    "nav article"
    "footer footer";

    grid-template-rows: 60px 1fr 100px;
    grid-template-columns: 20% 1fr;
    grid-gap: 1vw;

  margin: 0;
  background-color: rgb(158, 233, 252);
}
.content {
  grid-area: article;

  font-size: 1em;
  text-align: start;
  background-color: rgb(51, 53, 57);
  border-radius: 5px;
}
